import interfaces from '../../utils/interface.js'







export default {
	data() {
		return {
			mobile: '',
			verify: '',
			password: '',
			logining: true,
			isVerify: false, //显示获取验证码
			toVerify: false, //验证码输入
			toRes: false, //输入密码成功
			miaos: 9,
			miao: 5,
			hint: '获取验证码',
			clear: false,
			yan_zheng: ''
		}
	},
	onLoad() {

	},
	methods: {

		inputChange(e) {
			const key = e.currentTarget.dataset.key; //监控输入
			this[key] = e.detail.value; //号码
			const {
				mobile
			} = this;

			var reg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (reg.test(mobile)) {
				this.isVerify = true;
			} else {
				this.isVerify = false;
			}

		},
		navBack() {
			setTimeout(() => {
				uni.navigateTo({
					url: './login'
				})
			}, 500)
		},
		time() {
			this.hint = `${this.miao}${this.miaos}s后可重新获取`
			this.miaos--;
			if (this.miaos < 0) {
				this.miaos = 9;
				this.miao--;
				if (this.miao < 0) {
					this.miao = 0;
					this.miaos = 0;
					this.isVerify = true;
					this.hint = '获取验证码';
					this.clear = true;
					return;
				}
			}
		},
		huoqu() {
			//获取验证码
			let tell = this.mobile;
			this.request({
				url: interfaces.get_yan,
				method: "POST",
				data: {
					tell
				},
				success: ((res) => {
					console.log(res)
					if (res.error_code == 0) {
						this.yan_zheng = res.data;
						this.logining = false;
						this.isVerify = false; //60秒后 可重新获取
						let self = this;
						let timer = setInterval(function() {
							if (self.clear) {
								// console.log('清除')
								clearInterval(timer)
							} else {
								self.time();
							}
						}, 1000)
						uni.showToast({
							icon: "success",
							title: "已发送,请注意查收",
							duration: 1500,
						})
					} else {
						uni.showToast({
							icon: "none",
							title: res.data,
							duration: 1500,
						})
					}
				})
			});





















		},
		async toRegister() {
			// this.logining = true;
			const {
				mobile,
				verify,
				password
			} = this;

			if (!mobile) {
				uni.showToast({
					icon: "none",
					title: "请输入手机号"
				})
				return;
			} else if (!verify) {
				uni.showToast({
					icon: "none",
					title: "请输入验证码"
				})
				return;
			} else if (verify != this.yan_zheng) {
				//验证验证码验证码	
				uni.showToast({
					icon: "none",
					title: "验证码错误",
					duration: 1500,
				})
			} else {
				this.request({
					url: interfaces.set_password,
					method: "POST",
					data: {
						tell: this.mobile,
						password: this.password
					},
					success: ((res) => {
						if (res.error_code == 0) {
							uni.showToast({
								icon: "success",
								title: "注册成功!",
								duration: 1000,
							})
							let self = this;
							let isReg = true;
							setTimeout(function() {
								uni.navigateTo({
									url: './login?isReg=' + isReg
								}) //跳转登录
							}, 1000)
						}
					})
				});
				//h5






















			}
		}
	},

}
